export const handleSelectDate = (dates, date, handleSelect) => {
  console.log("newDates, date", dates, date);

  if (dates && Object.keys(dates).includes(date)) {
    const newDates = { ...dates };
    delete newDates[date];
    return newDates;
  }
  return handleSelect({ ...dates, [new Date(date)]: [] });
};
